a.navbar-brand {
    white-space: normal;
    text-align: center;
    word-break: break-all;
}

html {
    font-size: 14px;
}

@media (min-width: 768px) {
    html {
        font-size: 16px;
    }
}

.box-shadow {
    box-shadow: 0 .25rem .75rem rgba(0, 0, 0, .05);
}

button {
    background: none !important;
    border: none;
    padding: 0 !important;
    cursor: pointer;
    
}

.dropdown {
    padding: 0.5rem 0.5rem;
}

.dropdown-button {
    border:0;
}

svg {
    padding-bottom:2px;
}